import React from "react";

export function divideElements(elements: JSX.Element[] | undefined, divider: JSX.Element): React.ReactNode {
  return elements?.map((e, i) => {
    const isLast = elements?.length === i + 1;
    return !isLast ? (
      <>
        {e}
        {divider}
      </>
    ) : (
      e
    );
  });
}
