import _ from "lodash";
import { Pairs, Pairs_bityCryptos, Pairs_bityFiats, Pairs_paraswapTokens } from "../types";

import chfLogo from "./assets/338-3383164_flag-of-switzerland-logo-png-transparent-cross.png";
import btcLogo from "./assets/Bitcoin-Logo.png";
import eurLogo from "./assets/european-union-logo-vector.png";

export type Currency = {
  symbol: string;
  img: string;
  decimals: number;
  source: "bity" | "paraswap";
};

export const EUR: Currency = { img: eurLogo, source: "bity", symbol: "EUR", decimals: 0 };
export const BTC: Currency = { img: btcLogo, source: "bity", symbol: "BTC", decimals: 0 };

export function GetFiats(data: Pairs, search: string = ""): Currency[] {
  var currencies = data.bityFiats;

  if (search !== "") {
    search = search.toUpperCase();
    currencies = currencies.filter((t) => t.code?.includes(search));
  }

  return bityToCurrency(currencies);
}

export function GetCryptos(data: Pairs, search: string = ""): Currency[] {
  var bityCryptos = data.bityCryptos;
  var paraswapCryptos = data.paraswapTokens.filter((t) => bityCryptos.map((s) => s.code).every((s) => s !== t.symbol));

  if (search !== "") {
    search = search.toUpperCase();
    bityCryptos = bityCryptos.filter((t) => t.code?.includes(search));
    paraswapCryptos = paraswapCryptos.filter((t) => t.symbol?.includes(search));
  }

  return [...bityToCurrency(bityCryptos), ...paraswapToCurrency(paraswapCryptos)];
}

function bityToCurrency(bity: (Pairs_bityFiats | Pairs_bityCryptos)[]): Currency[] {
  return bity.map((e) => ({ img: getBityImage(e.code || ""), symbol: e.code || "", source: "bity", decimals: getBityDecimals(e.code || "") }));
}

function paraswapToCurrency(paraswap: Pairs_paraswapTokens[]): Currency[] {
  return paraswap.map((e) => ({ img: e.img || "", symbol: e.symbol || "", source: "paraswap", decimals: e.decimals || 0 }));
}

function getBityImage(symbol: string): string {
  const images: { [id: string]: string } = {
    BTC: btcLogo,
    USDC: "https://img.paraswap.network/USDC.png",
    ETH: "https://img.paraswap.network/ETH.png",
    EUR: eurLogo,
    CHF: chfLogo,
  };

  return images[symbol];
}

function getBityDecimals(symbol: string): number {
  const decimals: { [id: string]: number } = {
    BTC: 8,
    ETH: 18,
    USDC: 6,
  };

  return decimals[symbol];
}
