import { Button } from "@chakra-ui/button";
import { Box, Center, Divider, Flex, Text } from "@chakra-ui/layout";
import { RowData } from "../components/RowData";
import { MoneyInput } from "../components/MoneyInput";
import { useExchangeCurrency } from "../hooks/useExchangeCurrency";
import ReactCardFlip from "react-card-flip";
import { useDisclosure } from "@chakra-ui/hooks";
import { Card } from "../components/Card";
import { store } from "../store";
import React, { Fragment, useCallback, useContext, useEffect, useState } from "react";
import { FaPlus } from "react-icons/fa";
import Icon from "@chakra-ui/icon";
import { BankCard } from "../components/BankCard";
import { BankAccountForm } from "../components/BankAccountForm";
import { PaymentForm } from "../components/PaymentForm";
import { CloseButton } from "@chakra-ui/close-button";
import gql from "graphql-tag";
import { useMutation, useQuery } from "@apollo/client";
import { Exchange, ExchangeVariables, Pairs, PlaceOrder, PlaceOrderVariables } from "../types";
import { BTC, EUR, GetCryptos, GetFiats } from "../data/Currency";
import { BeatLoader } from "react-spinners";
import { isEmpty } from "lodash";
import CurrencyFormat from "react-currency-format";
import { divideElements } from "../utils/utils";
import { setCurrentOrder } from "../store/Action";
import { BankAccount } from "../store/State";

const GET_PAIRS = gql`
  query Pairs {
    bityFiats: getBityFiats {
      code
    }
    bityCryptos: getBityCryptos {
      code
    }
    paraswapTokens: getParaswapTokens {
      symbol
      img
      decimals
    }
  }
`;

const GET_EXCHANGE = gql`
  query Exchange($from: CurrencyInput!, $to: CurrencyInput!) {
    getExchange(from: $from, to: $to) {
      rate
      slippage
      fee {
        amount
        symbol
      }
      routing {
        from {
          amount
          symbol
        }
        to {
          amount
          symbol
        }
        source
      }
    }
  }
`;

const PLACE_ORDER = gql`
  mutation PlaceOrder(
    $input: PlaceOrderCurrency!
    $output: PlaceOrderCurrency!
    $account: BankAccount!
  ) {
    placeOrder(input: $input, output: $output, account: $account) {
      id
    }
  }
`;

export default function ExchangeView() {
  const initialExchangeAmount = 200;
  const { state, dispatch } = useContext(store);

  const { data: pairs, loading: loadingPairs } = useQuery<Pairs>(GET_PAIRS);
  const [placeOrder, { data: order, loading: loadingOrder, reset: resetOrder }] = useMutation<
    PlaceOrder,
    PlaceOrderVariables
  >(PLACE_ORDER);

  const handlerFrom = useExchangeCurrency(0, BTC);
  const handlerTo = useExchangeCurrency(initialExchangeAmount, EUR);

  const [exchangeFrom, setExchangeFrom, currencyFrom, ___, searchFrom] = handlerFrom;
  const [exchangeTo, setExchangeTo, currencyTo] = handlerTo;
  const [account, setAccount] = useState<BankAccount | null>(null);

  const [focus, setFocus] = useState<"FROM" | "TO">("TO");

  const [from, setFrom] = useState<string>(BTC.symbol);
  const [to, setTo] = useState<string>(EUR.symbol);

  const [amountFrom, setAmountFrom] = useState<number>();
  const [amountTo, setAmountTo] = useState<number | undefined>(initialExchangeAmount);

  const handleFocusFrom = useCallback(
    (symbol: string, amount: number) => {
      if (!isEmpty(symbol)) {
        setFrom(symbol);
      }
      if (!isNaN(amount)) {
        setAmountFrom(amount);
        setAmountTo(undefined);
        setFocus("FROM");
      }
    },
    [setFocus, setFrom, setTo, from]
  );

  const handleFocusTo = useCallback(
    (symbol: string, amount: number) => {
      if (!isEmpty(symbol)) {
        setTo(symbol);
      }
      if (!isNaN(amount)) {
        setAmountFrom(undefined);
        setAmountTo(amount);
        setFocus("TO");
      }
    },
    [setFocus, setFrom, setTo, from]
  );

  const { data: exchange, loading: loadingExchange } = useQuery<Exchange, ExchangeVariables>(
    GET_EXCHANGE,
    {
      variables: {
        from: {
          amount: amountFrom,
          symbol: from,
          decimals: currencyFrom.decimals,
        },
        to: {
          amount: amountTo,
          symbol: to,
          decimals: currencyTo.decimals,
        },
      },
    }
  );

  const value = amountFrom || amountTo || 0;
  const rate = exchange?.getExchange?.rate || 0;

  useEffect(() => {
    if (focus === "FROM") setExchangeTo(rate);
    if (focus === "TO") setExchangeFrom(rate);
  }, [rate]);

  const { isOpen: isFlipped, onToggle } = useDisclosure();
  const {
    isOpen: isOpenBankAccountForm,
    onClose: onCloseBankAccountForm,
    onOpen: onOpenBankAccountForm,
  } = useDisclosure();
  const { isOpen: isOpenPaymentForm, onClose: onClosePaymentForm } = useDisclosure();

  const cleanAccount = useCallback(() => setAccount(null), [setAccount]);

  const isPaying = account !== null;
  const source: Pairs = pairs ? pairs : { bityCryptos: [], bityFiats: [], paraswapTokens: [] };

  const fee = exchange?.getExchange.fee;
  const slippage = exchange?.getExchange.slippage;

  const handlePlaceOrder = async () => {
    if (!account || !exchangeFrom) {
      console.log({ account, exchangeFrom });
      return;
    }
    const { data, errors } = await placeOrder({
      variables: {
        input: {
          currency: currencyFrom.symbol,
          amount: amountFrom,
        },
        output: {
          currency: currencyTo.symbol,
          amount: amountTo,
        },
        account: {
          iban: account.iban,
          owner: {
            address: account.address,
            city: account.city,
            country: account.country.value,
            name: account.fullName,
            zip: account.postalCode,
          },
        },
      },
    });
    if (errors) console.log({ errors });
    if (data?.placeOrder.id) dispatch(setCurrentOrder(data.placeOrder.id));
    resetOrder();
  };

  const exchangeCard = (
    <Card>
      {!isPaying && (
        <Fragment>
          <MoneyInput
            handler={handlerFrom}
            setFocus={handleFocusFrom}
            label="From"
            modalLabel={"Select crypto"}
            allowedCurrencies={GetCryptos(source, searchFrom)}
            disabled={loadingPairs || (loadingExchange && focus === "TO")}
            isSearch
          />
          <MoneyInput
            handler={handlerTo}
            setFocus={handleFocusTo}
            label="To"
            modalLabel={"Select currency"}
            allowedCurrencies={GetFiats(source)}
            disabled={loadingPairs || (loadingExchange && focus === "FROM")}
            isSearch={false}
          />
        </Fragment>
      )}

      {isPaying ? (
        <Button bgColor="white" textColor="gray.500" rounded={25} mb={5} onClick={cleanAccount}>
          Back
        </Button>
      ) : (
        <Button colorScheme="teal" rounded={25} mb={5} onClick={onToggle}>
          Exchange
        </Button>
      )}

      <Box background={"gray.200"} maxW="sm" minH={166} borderRadius={25} p={5}>
        {exchange ? (
          <Fragment>
            <RowData
              left={
                <Text fontSize="12px" fontWeight="400" textAlign="left">
                  Rate
                </Text>
              }
              right={
                <Text fontSize="12px" fontWeight="300" textAlign="left">
                  <CurrencyFormat
                    prefix="~"
                    value={exchangeTo / exchangeFrom}
                    decimalScale={10}
                    displayType="text"
                  />{" "}
                  {currencyTo.symbol}/{currencyFrom.symbol}
                </Text>
              }
            />
            {fee && (
              <RowData
                left={
                  <Text fontSize="12px" fontWeight="400" textAlign="left">
                    Exchange fee
                  </Text>
                }
                right={
                  <Text fontSize="12px" fontWeight="300" textAlign="left">
                    <CurrencyFormat value={fee.amount} decimalScale={10} displayType="text" />{" "}
                    {fee.symbol}
                  </Text>
                }
              />
            )}
            {slippage && (
              <RowData
                left={
                  <Text fontSize="12px" fontWeight="400" textAlign="left">
                    Slippage tolerance
                  </Text>
                }
                right={
                  <Text fontSize="12px" fontWeight="300" textAlign="left">
                    {slippage}%
                  </Text>
                }
              />
            )}
            <Text fontSize="12px" fontWeight="300" textAlign="center" mt={3} p={3}>
              ORDER ROUTING
            </Text>
            {divideElements(
              exchange.getExchange?.routing?.map((r) => (
                <RowData
                  left={
                    <Text fontSize="12px" fontWeight="400" textAlign="left">
                      <CurrencyFormat value={r?.from?.amount} decimalScale={8} displayType="text" />{" "}
                      {r?.from?.symbol}
                      {" > "}
                      <CurrencyFormat
                        value={r?.to?.amount}
                        decimalScale={8}
                        displayType="text"
                      />{" "}
                      {r?.to?.symbol}
                    </Text>
                  }
                  right={
                    <Text fontSize="10px" fontWeight="300" textAlign="left">
                      via {r?.source}
                    </Text>
                  }
                />
              )),
              <Divider borderColor="gray.400" mt={2} mb={2} />
            )}
          </Fragment>
        ) : (
          <Center height={120}>
            <BeatLoader size={8} color="teal" />
          </Center>
        )}
      </Box>

      {isPaying && (
        <Fragment>
          <Box background={"gray.200"} maxW="sm" borderRadius={25} p={5} mt={5}>
            <Text fontWeight="800" fontSize="15" textColor="gray.600" mb={2}>
              {account.iban}
            </Text>
            <RowData
              alignItems="flex-end"
              left={
                <Text fontWeight="600" fontSize="12" textColor="gray.500" mr={5}>
                  {account.bic}
                </Text>
              }
              right={
                <Text fontWeight="600" fontSize="8" textColor="gray.400">
                  {account.bank}
                </Text>
              }
            />
          </Box>
          <Button colorScheme="blue" rounded={25} mt={5} onClick={handlePlaceOrder}>
            {loadingOrder ? "Creating..." : "Pay"}
          </Button>
        </Fragment>
      )}
    </Card>
  );

  const bankAccountCard = (
    <Card closeButton={<CloseButton onClick={onToggle} />}>
      <Flex direction="column">
        <BankCard onClick={onOpenBankAccountForm}>
          <Center p={5}>
            <Icon as={FaPlus} color="gray.400" w={7} h={7} />
          </Center>
        </BankCard>
        {state.accounts.map((a) => (
          <BankCard
            key={a.id}
            onClick={() => {
              setAccount(a);
              onToggle();
            }}
          >
            <Text fontWeight="800" fontSize="25" textColor="gray.500" mb={2}>
              {a.iban}
            </Text>
            <RowData
              alignItems="flex-end"
              left={
                <Text fontWeight="600" fontSize="15" textColor="gray.400" mr={5}>
                  {a.bic}
                </Text>
              }
              right={
                <Text fontWeight="600" fontSize="10" textColor="gray.300">
                  {a.bank}
                </Text>
              }
            />
          </BankCard>
        ))}
      </Flex>
    </Card>
  );

  return (
    <Fragment>
      <BankAccountForm isOpen={isOpenBankAccountForm} onClose={onCloseBankAccountForm}>
        Add account
      </BankAccountForm>
      {state.currentOrder && (
        <PaymentForm
          isOpen={isOpenPaymentForm}
          onClose={onClosePaymentForm}
          id={state.currentOrder}
        >
          Send Payment
        </PaymentForm>
      )}
      <ReactCardFlip isFlipped={isFlipped}>
        {exchangeCard}
        {bankAccountCard}
      </ReactCardFlip>
    </Fragment>
  );
}
