import { Flex } from "@chakra-ui/layout";
import { FC } from "react";

interface CardProps {
  closeButton?: React.ReactNode;
}

export const Card: FC<CardProps> = ({ children, ...props }) => (
  <Flex height="100vh" justifyContent="center" alignItems="flex-start">
    <Flex direction="column" background={"gray.100"} rounded={25} pt={10} pb={10} pr={5} pl={5} mt="15vh" mb="15vh" minHeight={500} minWidth={400}>
      {props.closeButton&& <Flex justifyContent="right" mt={-7} mr={-2} mb={5}>{props.closeButton}</Flex>}
      {children}
    </Flex>
  </Flex>
);
