export interface Order {}

export interface CountryData {
  label: string;
  value: string;
}

export interface BankAccount {
  id: string;
  iban: string;
  bic: string;
  bank: string;

  email: string;
  fullName: string;
  address: string;
  postalCode: string;
  city: string;
  country: CountryData;
}

export interface State {
  accounts: BankAccount[];
  orders: Order[];
  currentOrder?: string;
}

export const initialState: State = {
  accounts: [],
  orders: [],
  currentOrder: undefined,
};
