import { Button } from "@chakra-ui/button";
import { useControllableState, useDisclosure } from "@chakra-ui/hooks";
import { Input, InputGroup, InputRightElement } from "@chakra-ui/input";
import { Box, Flex, Text } from "@chakra-ui/layout";
import { Modal, ModalBody, ModalCloseButton, ModalContent, ModalHeader, ModalOverlay } from "@chakra-ui/modal";
import { Fragment, useCallback, useEffect } from "react";
import { FaChevronDown } from "react-icons/fa";
import { CloseButton } from "@chakra-ui/close-button";
import { CurrencyItem } from "./CurrencyItem";
import { Currency } from "../data/Currency";
import { CurrencyHandler } from "../hooks/useExchangeCurrency";

interface MoneyInputProps {
  label: string;
  disabled?: boolean;
  modalLabel: string;
  isSearch?: boolean;
  handler: CurrencyHandler;
  isFiat?: boolean;
  isCrypto?: boolean;
  setFocus: (symbol: string, amount: number) => void;
  allowedCurrencies: Currency[];
}

export const MoneyInput = (props: MoneyInputProps) => {
  const { label, modalLabel, isSearch, handler, allowedCurrencies, setFocus } = props;
  const [value, setValue, symbol, setSymbol, search, setSearch] = handler;

  const [amount, setAmount] = useControllableState<string>({ defaultValue: value.toString() });
  const { isOpen, onOpen, onClose } = useDisclosure();

  const handleSetValue = useCallback((e: any) => {
    setAmount(e.target.value);
    const newVal = parseFloat(e.target.value);
    if (!isNaN(newVal)) {
      setValue(newVal);
      setFocus("", newVal);
    }
  }, []);

  const handleSetSymbol = useCallback(
    (newSymbol: Currency) => () => {
      setSymbol(newSymbol);
      setSearch("");
      onClose();
      setFocus(newSymbol.symbol, NaN);
    },
    [setSymbol, setSearch, onClose, setFocus]
  );

  const handleCleanSearch = useCallback(() => {
    setSearch("");
  }, [setSearch]);

  useEffect(() => {
    setAmount(value.toString());
  }, [value]);

  const MoneyInputField = (
    <Box maxW="sm" background="white" borderRadius={25} p={5} mb={5}>
      <Text fontSize="13px" fontWeight="300">
        {label}
      </Text>
      <Box display="flex">
        <Input
          type="number"
          variant="unstyled"
          borderWidth={0}
          fontSize={24}
          fontWeight={300}
          onChange={handleSetValue}
          value={amount}
          disabled={props.disabled}
          step={props.isFiat ? "0.01" : "0.00000001"}
        />
        <Button
          rightIcon={<FaChevronDown />}
          background="inherit"
          onClick={onOpen}
          disabled={props.disabled}
          leftIcon={<img src={symbol.img} alt="" style={{ width: 25, margin: 2 }} />}
        >
          {symbol.symbol}
        </Button>
      </Box>
    </Box>
  );

  const MoneyInputModal = (
    <Modal isOpen={isOpen} onClose={onClose} isCentered scrollBehavior="inside">
      <ModalOverlay />
      <ModalContent rounded={25} pb={25} minH="50vh">
        <ModalHeader alignItems="center">{modalLabel}</ModalHeader>
        <ModalCloseButton />
        <ModalBody>
          {isSearch && (
            <InputGroup size="md" mb={8}>
              <Input placeholder="Search" value={search} onChange={(e) => setSearch(e.target.value)} borderRadius={25} />
              <InputRightElement width="4.5rem">
                <CloseButton onClick={handleCleanSearch} />
              </InputRightElement>
            </InputGroup>
          )}
          <Flex direction="column">
            {allowedCurrencies.map((currency) => (
              <CurrencyItem value={currency} onClick={handleSetSymbol(currency)} />
            ))}
          </Flex>
        </ModalBody>
      </ModalContent>
    </Modal>
  );

  return (
    <Fragment>
      {MoneyInputField}
      {MoneyInputModal}
    </Fragment>
  );
};
