import React, { useContext, useEffect } from "react";
import ReactDOM from "react-dom";
import reportWebVitals from "./reportWebVitals";
import { ChakraProvider } from "@chakra-ui/react";
import { AppRoutes } from "./routes";
import StoreProvider, { store } from "./store";
import { ApolloClient, ApolloProvider, from, HttpLink, InMemoryCache } from "@apollo/client";
import { useCookies } from "react-cookie";
import { onError } from "@apollo/client/link/error";

import config from "./aws-exports";
import { setCurrentOrder } from "./store/Action";
import { BITY_CURRENT_ORDER_COOKIE } from "./data/constants";

const { endpoint } = config.aws_cloud_logic_custom[0];

const httpLink = new HttpLink({
  uri: endpoint + "/graphql",
});

const errorLink = onError(({ graphQLErrors, networkError }) => {
  if (graphQLErrors)
    graphQLErrors.forEach(({ message, locations, path }) =>
      console.log(`[GraphQL error]: Message: ${message}, Location: ${locations}, Path: ${path}`)
    );

  if (networkError) console.log(`[Network error]: ${networkError}`);
});

const client = new ApolloClient({ link: from([httpLink, errorLink]), cache: new InMemoryCache() });

const InitApp = () => {
  const { state, dispatch } = useContext(store);
  const [cookies, setCookie] = useCookies([BITY_CURRENT_ORDER_COOKIE]);

  useEffect(() => {
    if (cookies.bity_current_order) dispatch(setCurrentOrder(cookies.bity_current_order));
  }, [cookies.bity_current_order]);

  useEffect(() => {
    if (state.currentOrder) setCookie(BITY_CURRENT_ORDER_COOKIE, state.currentOrder);
  }, [state.currentOrder]);

  return (
    <ApolloProvider client={client}>
      <AppRoutes />
    </ApolloProvider>
  );
};

ReactDOM.render(
  <React.StrictMode>
    <ChakraProvider>
      <StoreProvider>
        <InitApp />
      </StoreProvider>
    </ChakraProvider>
  </React.StrictMode>,
  document.getElementById("root")
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
