import { BankAccount, Order } from "./State";

export interface Action {
  type: string;
}

export interface Payload<T> extends Action {
  payload: T;
}

export const ADD_ORDER = "ADD_ORDER";

export function addOrder(payload: Order): Payload<Order> {
  return {
    type: ADD_ORDER,
    payload,
  };
}

export const CLEAN_ORDERS = "CLEAN_ORDERS";

export function cleanOrders(): Action {
  return {
    type: CLEAN_ORDERS,
  };
}

export const SET_CURRENT_ORDER = "SET_CURRENT_ORDER";

export function setCurrentOrder(payload: string): Payload<string> {
  return {
    type: SET_CURRENT_ORDER,
    payload,
  };
}

export const CLEAN_CURRENT_ORDER = "CLEAN_CURRENT_ORDER";

export function cleanCurrentOrder(): Action {
  return { type: CLEAN_CURRENT_ORDER };
}

export const ADD_BANK_ACCOUNT = "ADD_BANK_ACCOUNT";

export function addBankAccount(payload: BankAccount): Payload<BankAccount> {
  return {
    type: ADD_BANK_ACCOUNT,
    payload,
  };
}

export const CLEAN_BANK_ACCOUNTS = "CLEAN_BANK_ACCOUNTS";

export function cleanBankAccounts(): Action {
  return {
    type: CLEAN_ORDERS,
  };
}
