import { gql, useQuery } from "@apollo/client";
import {
  Modal,
  ModalBody,
  ModalCloseButton,
  ModalContent,
  ModalHeader,
  ModalOverlay,
  ModalProps,
} from "@chakra-ui/modal";
import { useContext } from "react";
import { store } from "../store";
import { cleanCurrentOrder } from "../store/Action";
import { GetOrder, GetOrderVariables } from "../types";
import QRCode from "react-qr-code";
import {
  Box,
  Button,
  Center,
  Flex,
  Popover,
  PopoverBody,
  PopoverContent,
  PopoverTrigger,
  Text,
  Tooltip,
  useClipboard,
} from "@chakra-ui/react";
import { BeatLoader } from "react-spinners";
import { BITY_CURRENT_ORDER_COOKIE } from "../data/constants";
import { useCookies } from "react-cookie";
import { RowData } from "./RowData";
import CurrencyFormat from "react-currency-format";
import Clock from "./Countdown";

const GET_ORDER = gql`
  query GetOrder($id: ID!) {
    getBityOrder(id: $id) {
      id
      input {
        amount
        currency
      }
      output {
        amount
        currency
        iban
      }
      payment_details {
        crypto_address
      }
      timestamp_awaiting_payment_since
      timestamp_created
      timestamp_price_guaranteed
    }
  }
`;

interface PaymentFormProps {
  id: string;
}

export const PaymentForm: React.FC<ModalProps & PaymentFormProps> = ({ id, children }) => {
  const { state, dispatch } = useContext(store);
  const [_, __, deleteCookie] = useCookies([BITY_CURRENT_ORDER_COOKIE]);

  const { data, loading } = useQuery<GetOrder, GetOrderVariables>(GET_ORDER, {
    variables: {
      id,
    },
  });

  const { hasCopied, onCopy } = useClipboard(
    data?.getBityOrder.payment_details?.crypto_address || ""
  );

  const onClose = () => dispatch(cleanCurrentOrder());

  const handleCancelOrder = () => {
    deleteCookie(BITY_CURRENT_ORDER_COOKIE);
    dispatch(cleanCurrentOrder());
  };

  const order = data?.getBityOrder;

  console.log({ time: order?.timestamp_awaiting_payment_since });

  return (
    <Modal isOpen={state.currentOrder !== undefined} onClose={onClose} isCentered>
      <ModalOverlay />
      <ModalContent rounded={25} pb={25} minH="50vh">
        <ModalHeader alignItems="center">{children}</ModalHeader>
        <ModalCloseButton />
        <ModalBody>
          {!order || loading ? (
            <Center height={400}>
              <BeatLoader size={8} color="teal" />
            </Center>
          ) : (
            <>
              <Text textAlign="justify" fontSize={15}>
                You have successfully created an order! Please perform the following payment within
                the remaining time and we will execute your order with the guaranteed exchange rate.
              </Text>
              <Box mt={5}>
                <RowData
                  alignItems="flex-end"
                  left={
                    <Flex flexDirection="column">
                      <Text color="gray.500">Remaining time:</Text>
                      <Text fontSize={25} fontWeight={200}>
                        <Clock deadline={Date.parse(order.timestamp_price_guaranteed || "")} />
                      </Text>
                    </Flex>
                  }
                  right={
                    <Flex flexDirection="column" alignItems="flex-end">
                      <Text color="gray.500">Amount to send:</Text>
                      <Text fontSize={25} fontWeight={200}>
                        <CurrencyFormat
                          value={order.input?.amount}
                          decimalScale={8}
                          displayType="text"
                        />{" "}
                        {order.input?.currency}
                      </Text>
                    </Flex>
                  }
                />
              </Box>
              <Center mt={10}>
                <Tooltip label="Copy address to clipboard">
                  <Popover isOpen={hasCopied} placement="bottom">
                    <PopoverTrigger>
                      <Button onClick={onCopy} variant="outline">
                        {order.payment_details?.crypto_address}
                      </Button>
                    </PopoverTrigger>
                    <PopoverContent bg="gray.700" color="white">
                      <PopoverBody textAlign="center">
                        <Text>Address copied to clipboard</Text>
                      </PopoverBody>
                    </PopoverContent>
                  </Popover>
                </Tooltip>
              </Center>
              <Center mt={10}>
                <QRCode
                  value={`bitcoin:${order.payment_details?.crypto_address}?amount=${order.input?.amount}`}
                />
              </Center>
              <Center mt={10}>
                <Button colorScheme="red" rounded={25} onClick={handleCancelOrder} isFullWidth>
                  Cancel
                </Button>
              </Center>
            </>
          )}
        </ModalBody>
      </ModalContent>
    </Modal>
  );
};
