import { Reducer } from "react";
import { Action, ADD_BANK_ACCOUNT, ADD_ORDER, CLEAN_BANK_ACCOUNTS, CLEAN_CURRENT_ORDER, CLEAN_ORDERS, Payload, SET_CURRENT_ORDER } from "./Action";
import { BankAccount, Order, State } from "./State";

export const reducer: Reducer<State, Action> = (state, action) => {
  switch (action.type) {
    case ADD_ORDER: {
      const { payload: order } = action as Payload<Order>;
      return { ...state, orders: [...state.orders, order] };
    }
    case CLEAN_ORDERS: {
      return { ...state, orders: [] };
    }
    case SET_CURRENT_ORDER: {
      const { payload: currentOrder } = action as Payload<string>;
      return { ...state, currentOrder };
    }
    case CLEAN_CURRENT_ORDER: {
      return { ...state, currentOrder: undefined };
    }
    case ADD_BANK_ACCOUNT: {
      const { payload: account } = action as Payload<BankAccount>;
      return { ...state, accounts: [...state.accounts, account] };
    }
    case CLEAN_BANK_ACCOUNTS: {
      return { ...state, accounts: [] };
    }
    default: {
      return state;
    }
  }
};
