import { useControllableState } from "@chakra-ui/hooks";
import { Currency } from "../data/Currency";

export type CurrencyHandler = [
  number,
  React.Dispatch<React.SetStateAction<number>>,
  Currency,
  React.Dispatch<React.SetStateAction<Currency>>,
  string,
  React.Dispatch<React.SetStateAction<string>>
];

export const useExchangeCurrency = (initialAmount: number, initialCurrency: Currency): CurrencyHandler => {
  const [amount, setAmount] = useControllableState({ defaultValue: initialAmount });
  const [currency, setCurrency] = useControllableState({ defaultValue: initialCurrency });
  const [search, setSearch] = useControllableState({ defaultValue: "" });

  return [amount, setAmount, currency, setCurrency, search, setSearch];
};
