import { store } from "../store";
import { useContext, useMemo } from "react";
import {
  Modal,
  ModalBody,
  ModalCloseButton,
  ModalContent,
  ModalHeader,
  ModalOverlay,
  ModalProps,
} from "@chakra-ui/modal";
import { useFormik } from "formik";
import { addBankAccount } from "../store/Action";
import { FormControl, FormHelperText, FormLabel } from "@chakra-ui/form-control";
import { Input } from "@chakra-ui/input";
import { Button } from "@chakra-ui/button";
import { Flex } from "@chakra-ui/layout";
import { BankAccount } from "../store/State";
import countryList from "react-select-country-list";
import Select from "react-select";

export const BankAccountForm: React.FC<ModalProps> = ({ isOpen, onClose, children }) => {
  const { dispatch } = useContext(store);
  const countries = useMemo(() => countryList().getData(), []);

  const formik = useFormik({
    initialValues: {
      bank: "TRANSFER WISE",
      bic: "TRWIBA1XXX",
      // address: "Casut 122",
      // city: "Li Curt",
      // country: "DE",
      // fullName: "Doctor Monde",
      // postalCode: "7745",
      // email: "user@host.com",
      // iban: "CH5189144335186753448",
    } as BankAccount,
    onSubmit: async (val, ctx) => {
      dispatch(addBankAccount(val));
      ctx.resetForm();
      onClose();
    },
  });

  console.log({ field: formik.values });

  return (
    <Modal isOpen={isOpen} onClose={onClose} isCentered>
      <ModalOverlay />
      <ModalContent rounded={25} pb={25} minH="50vh">
        <ModalHeader alignItems="center">{children}</ModalHeader>
        <ModalCloseButton />
        <ModalBody>
          <form onSubmit={formik.handleSubmit}>
            <FormControl id="email">
              <FormLabel>Email address</FormLabel>
              <Input
                id="email"
                name="email"
                type="email"
                value={formik.values.email}
                onChange={formik.handleChange}
                error={formik.touched.email && Boolean(formik.errors.email)}
                helperText={formik.touched.email && formik.errors.email}
              />
              <FormHelperText>We'll never share your email.</FormHelperText>
            </FormControl>
            <FormControl id="iban" pt={5}>
              <FormLabel>IBAN</FormLabel>
              <Input
                id="iban"
                name="iban"
                type="text"
                value={formik.values.iban}
                onChange={formik.handleChange}
                error={formik.touched.iban && Boolean(formik.errors.iban)}
                helperText={formik.touched.iban && formik.errors.iban}
              />
              <FormHelperText>We'll never share this information.</FormHelperText>
            </FormControl>

            <FormControl id="fullName" pt={5}>
              <FormLabel>Full name</FormLabel>
              <Input
                id="fullName"
                name="fullName"
                type="text"
                value={formik.values.fullName}
                onChange={formik.handleChange}
                error={formik.touched.fullName && Boolean(formik.errors.fullName)}
                helperText={formik.touched.fullName && formik.errors.fullName}
              />
            </FormControl>
            <FormControl id="address" pt={5}>
              <FormLabel>Address</FormLabel>
              <Input
                id="address"
                name="address"
                type="text"
                value={formik.values.address}
                onChange={formik.handleChange}
                error={formik.touched.address && Boolean(formik.errors.address)}
                helperText={formik.touched.address && formik.errors.address}
              />
            </FormControl>
            <FormControl id="city" pt={5}>
              <FormLabel>City</FormLabel>
              <Input
                id="city"
                name="city"
                type="text"
                value={formik.values.city}
                onChange={formik.handleChange}
                error={formik.touched.city && Boolean(formik.errors.city)}
                helperText={formik.touched.city && formik.errors.city}
              />
            </FormControl>
            <Flex>
              <FormControl id="postalCode" pt={5} w={250} mr={5}>
                <FormLabel>ZIP/Postal Code</FormLabel>
                <Input
                  id="postalCode"
                  name="postalCode"
                  type="text"
                  value={formik.values.postalCode}
                  onChange={formik.handleChange}
                  error={formik.touched.postalCode && Boolean(formik.errors.postalCode)}
                  helperText={formik.touched.postalCode && formik.errors.postalCode}
                />
              </FormControl>
              <FormControl id="country" pt={5}>
                <FormLabel>Country</FormLabel>
                <Select
                  id="country"
                  name="country"
                  placeholder="Select country"
                  value={formik.values.country}
                  onChange={(e) => formik.setFieldValue("country", e)}
                  // error={formik.touched.country && Boolean(formik.errors.country)}
                  // helperText={formik.touched.country && formik.errors.country}
                  options={countries as any}
                />
              </FormControl>
            </Flex>
            <Button mt={10} isFullWidth type="submit">
              Submit
            </Button>
          </form>
        </ModalBody>
      </ModalContent>
    </Modal>
  );
};
