import { Text } from "@chakra-ui/react";
import React, { Component } from "react";

interface ClockProps {
  deadline: string | number;
}

interface ClockState {
  days: number;
  hours: number;
  minutes: number;
  seconds: number;
  time: number;
}

export default class Clock extends Component<ClockProps, ClockState> {
  constructor(props: any) {
    super(props);
    this.state = { days: 0, hours: 0, minutes: 0, seconds: 0, time: 0 };
  }
  componentWillMount() {
    this.getTimeUntil(this.props.deadline);
  }
  componentDidMount() {
    setInterval(() => this.getTimeUntil(this.props.deadline), 1000);
  }
  leading0(num: number) {
    return num < 10 ? "0" + num : num;
  }
  getTimeUntil(deadline: string | number) {
    if (typeof deadline === "string") {
      deadline = Date.parse(deadline);
    }
    const time = deadline - Date.parse(new Date().toString());
    if (time < 0) {
      this.setState({ days: 0, hours: 0, minutes: 0, seconds: 0, time: 0 });
    } else {
      const seconds = Math.floor((time / 1000) % 60);
      const minutes = Math.floor((time / 1000 / 60) % 60);
      const hours = Math.floor((time / (1000 * 60 * 60)) % 24);
      const days = Math.floor(time / (1000 * 60 * 60 * 24));

      this.setState({ days, hours, minutes, seconds, time });
    }
  }
  render() {
    return (
      <Text>
        {this.leading0(this.state.minutes)} : {this.leading0(this.state.seconds)}
      </Text>
    );
  }
}
