import { Box, Flex, Text } from "@chakra-ui/layout";
import { Currency } from "../data/Currency";

export function CurrencyItem(props: { value: Currency; onClick: () => void }) {
  return (
    <Box
      as="button"
      transition="all 0.2s cubic-bezier(.08,.52,.52,1)"
      borderRadius="2px"
      fontSize="14px"
      fontWeight="semibold"
      bg="white"
      color="gray"
      textAlign="left"
      p={5}
      _hover={{ bg: "#ebedf0" }}
      onClick={props.onClick}
    >
      <Flex alignItems="center">
        <img src={props.value.img} alt="" style={{ width: 30 }} />
        <Text ml={4}>{props.value.symbol}</Text>
      </Flex>
    </Box>
  );
}
